import React from 'react'
import './HeaderBg.css'


const HeaderBg = () => {
  return (
    <section id="home">

        <div className="container-fluid background">
            <div className="row">
                <div className="col-lg-8 col-md-6- col-sm-12 bg-description ">
                    <h5 className="animate__animated animate__fadeInUp animate__delay-1s">EXPERT GUIDANCE</h5>
                    <h1 className="animate__animated animate__fadeInUp animate__delay-2s">Where Your Dreams Find a Home</h1>
                    <p className="animate__animated animate__fadeInUp animate__delay-2s">Your real estate journey begins with a trusted partner. We are committed to going the extra
                        mile for our clients.
                        Your satisfaction is our top priority.</p>

                    <a href="#about" className="animate__animated animate__fadeInUp animate__delay-3s">Learn More <span></span></a>
                </div>
            </div>

        </div>
    </section>
  )
}

export default HeaderBg