import React from 'react'
import './Css/Sale.css'
import salesData from '../Components/Assets/sale'
import Card from '../Components/Card/Card'

export default function Sale(props) {
    return (
        <div>
            <div className="container-fluid backgroundImgSale">
                <div className="row">
                <div className="col-lg-8 col-md-6- col-sm-12  bg-description">
                    <h5 className="animate__animated animate__fadeInUp animate__delay-1s">CRAFTED EXCELLENCE</h5>
                <h1 className="animate__animated animate__fadeInUp animate__delay-2s">Elevate Your Lifestyle: Exceptional Properties and Homes Await</h1>
                <p className="animate__animated animate__fadeInUp animate__delay-2s">Elevate your living experience with our curated selection of premium homes. Discover luxury, comfort, and style in every detail. Your dream property awaits.</p>
                </div>
                </div>
            </div>
            <div className="container">
            <div className="titles-property animate__animated animate__fadeInUp animate__delay-1s">
                <h6>Offers</h6>
                <h1><span>For Sale</span></h1>
            </div>
            </div>
            <div className="container cards">
                {salesData.map((item, i) => {
                    return <Card key={i}
                        name={item.name}
                        category={item.category}
                        size={item.size}
                        bedrooms={item.bedrooms}
                        bathrooms={item.bathrooms}
                        image={item.image}
                        id={item.id}
                        price={item.price}
                        area ={item.area}
                    />


                })}
            </div>
        </div>
    )
}

