import React from 'react';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper/modules';
import { Swiper as SwiperComponent, SwiperSlide } from 'swiper/react';
import { Link } from 'react-router-dom';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import 'swiper/css/autoplay';

import offersData from '../Assets/offers';
import './Offers.css'

const LinkStyle = {
  textDecoration: "none",
  backgroundColor: "black",
  outline: 'none',
};

const Offers = () => {



  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };
  return (
    <SwiperComponent
      modules={[Navigation, Pagination, Scrollbar, A11y]}

      spaceBetween={50}
      slidesPerView={3}
      enabled={true}
      breakpoints={{
        100: {
          slidesPerView: 1,
        },
        640: {
          slidesPerView: 2,
          spaceBetween: 30,
        },
        768: {
          slidesPerView: 2,
          spaceBetween: 30,
        },
        1024: {
          slidesPerView: 2,
          spaceBetween: 30,
        },
        1324: {
          slidesPerView: 3,
          spaceBetween: 30,
        },
      }}
      navigation
      pagination={{ el: '.swiper-custom-pagination' }}
    // onSwiper={(swiper) => console.log(swiper)}
    >
      <div className="">
        <div className="bg-white home-content swiper-content">


          <div className="home swiper mySwiper">
            {offersData.map((item) => (
              <SwiperSlide key={item.id}>
                {/* <Link to={`/product/${item.id}`}> */}
                <img src={item.image} alt="" /> {/* onClick={scrollToTop}*/}
                {/* </Link> */}
                {/* <Link className='btn'  to={`/product/${item.id}`}>View</Link> */}
                <div className="bg-whitee">
                  <h2>
                    {item.name} For ${item.price}
                    {item.category === "rent" ? "/month" : ""}
                  </h2>
                  <h5>Area: {item.area}</h5>

                  {/* <p>
                      <i className="ri-hotel-bed-line"></i>
                      {item.bedrooms} Bedrooms
                    </p>
                    <p>
                      <i className="fa-solid fa-toilet"></i>
                      {item.bathrooms} Bathrooms
                    </p> */}

                  {/* <h6>Size: {item.size}</h6> */}

                  <Link
                    onClick={scrollToTop}
                    className="read-btn"
                    style={LinkStyle}
                    to={`/product/${item.id}`}
                    data-aos="fade-up"
                  >
                    <i className="ri-arrow-right-line right-arrow"></i>View MORE
                    <span></span>
                  </Link>
                </div>


              </SwiperSlide>

            ))}
          </div>
          <div className="swiper-custom-pagination"></div>

        </div>
      </div>
    </SwiperComponent>

  );
};

export default Offers;
