import React, { useContext } from 'react'
import { ProductContext } from '../Context/ProductContext'
import { useParams } from 'react-router-dom'
import ProductDisplay from '../Components/ProductDisplay/ProductDisplay'
import SwiperProduct from '../Components/SwiperProduct/SwiperProduct'
import Breadcrum from '../Components/Breadcrums/Breadcrum'
import SwiperBgHeader from '../Components/SwiperHeaderBg/SwiperBg'

const Product = () => {

  const {all_offers } = useContext(ProductContext)
  const {productId} = useParams();
  const product = all_offers.find((e) => e.id=== Number(productId))


  return (
    <div>
      <SwiperBgHeader/>
      <Breadcrum  product={product}/>
      <ProductDisplay product = {product}/>
      <SwiperProduct product = {product}/>      
    </div>
  )
}

export default Product