import React from 'react';
import "./ProductDisplay.css";

const ProductDisplay = (props) => {
    const { product } = props;



    return (
        <>
            <div className='productdisplay container'>
                <div className="row">
                    <div className="col-lg-12 col-md-18- col-sm-12 title d-col-block d-sm-block">
                        <h1 className="animate__animated animate__fadeInUp animate__delay-1s">{product.name} For ${product.price}</h1>
                        <hr />

                    </div>
                    <div className="display-left col-lg-6 col-md-12 col-sm-12 col-xm-12">
                        <div className="bgImg">
                            <img className='animate__animated animate__fadeInUp animate__delay-1s' src={product.image} alt="" />

                        </div>

                        <div className="container">
                            <div className="row boxes">
                                <div className="box col-lg-3 col-md-3 col-sm-12 col-xm-12 animate__animated animate__fadeInUp animate__delay-1s">
                                    <div className="box-content">
                                        <p><i className="ri-hotel-bed-line"></i>{product.bedrooms}</p>

                                    </div>
                                </div>

                                <div className="box col-lg-3 col-md-3 col-sm-12 col-xm-12 animate__animated animate__fadeInUp animate__delay-2s">
                                    <div className="box-content">

                                        <p><i className="fa-solid fa-toilet"></i>{product.bathrooms}</p>
                                    </div>
                                </div>

                                <div className="box col-lg-3 col-md-3 col-sm-12 col-xm-12 animate__animated animate__fadeInUp animate__delay-3s">
                                    <div className="box-content">

                                        <p><i className="fa-solid fa-maximize"></i> {product.size}</p>
                                    </div>
                                </div>

                                <div className="box col-lg-3 col-md-3 col-sm-12 col-xm-12 animate__animated animate__fadeInUp animate__delay-4s">
                                    <div className="box-content">
                                        <p><i className="fa-solid fa-dollar-sign"></i>{product.price}</p>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>


                    <div className="display-right col-lg-6 col-md-12 col-sm-12 col-xm-12">
                        {/* <div className="col-lg-12 col-md-18- col-sm-12 title ">
                            <h1 className="animate__animated animate__fadeInUp animate__delay-1s">{product.name} For ${product.price}</h1>
                            <hr />

                        </div> */}

                        <div className="description animate__animated animate__fadeInUp animate__delay-1s">
                            <h1>Description</h1>
                            <div>
                                <pre>{product.description}</pre>

                            </div>
                        </div>
                    </div>
                </div>

            </div>

            <div className="container">
                <div className="description1 animate__animated animate__fadeInUp animate__delay-1s">
                    <h1>Description</h1>
                    <div>
                        <pre>{product.description}</pre>

                    </div>
                </div>
            </div>
            <div className="container">
                <div className="titles-product">
                    <h6>View More</h6>
                    <h1>
                        <span>Gallery</span>
                    </h1>
                </div>
            </div>
        </>
    );
};

export default ProductDisplay;





