import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import AOS from 'aos';
import 'aos/dist/aos.css';
import './Card.css';

const LinkStyle = {
  textDecoration: "none",
  // backgroundColor: "#404040",
  backgroundColor: "#000",
  outline: 'none',
};

const Card = (props) => {
  useEffect(() => {
    AOS.init();
  }, []);

  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };

  return (
    <div className="card" >
      <img  src={props.image} alt="sale property" />
      <div className="bg-white">
        <h2>
          {props.name} For ${props.price}
          {props.category === "rent" ? "/month" : ""}
        </h2>
        <h4>Area: {props.area}</h4>

        <p>
          <i className="ri-hotel-bed-line"></i>
          {props.bedrooms} Bedrooms
        </p>
        <p>
          <i className="fa-solid fa-toilet"></i>
          {props.bathrooms} Bathrooms
        </p>

        <h6>Size: {props.size}</h6>

        <Link
          onClick={scrollToTop}
          className="read-btn"
          style={LinkStyle}
          to={`/product/${props.id}`}
          data-aos="fade-up"
        >
          <i className="ri-arrow-right-line right-arrow"></i>READ MORE{" "}
          <span></span>
        </Link>
         
      </div>
    </div>
  );
};

export default Card;
