import React from 'react'
import './Css/Sale.css'
import salesData from '../Components/Assets/rentals'
import Card from '../Components/Card/Card'
export default function Sale(props) {
    return (
        <div>
            <div className="container-fluid backgroundImgRent">
                <div className="row">
                    <div className="col-lg-8 col-md-6- col-sm-12  bg-description">
                        <h5>Elite Expertise</h5>
                        <h1>Elevate Your Lifestyle: Premier Rental Residences</h1>
                        <p>Explore distinctive rental residences designed for your lifestyle. Your ideal lease is just a click away.</p>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="titles-property animate__animated animate__fadeInUp animate__delay-1s">
                    <h6>Offers</h6>
                    <h1><span>For Rent</span></h1>
                </div>
            </div>
            <div className="container cards">
                {salesData.map((item, i) => {
                    return <Card key={i}
                        category={item.category}
                        name={item.name}
                        size={item.size}
                        bedrooms={item.bedrooms}
                        bathrooms={item.bathrooms}
                        image={item.image}
                        id={item.id}
                        price={item.price}
                        area ={item.area}
                    />


                })}
            </div>
        </div>
    )
}