import React, { useRef, useEffect } from 'react';
import './Footer.css';
import emailjs from '@emailjs/browser';
import AOS from 'aos';
import 'aos/dist/aos.css';

const Footer = () => {
  useEffect(() => {
    AOS.init();
  }, []);

  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_cbn949w', 'template_7ackyi9', form.current, 'HRdR0xy8NhjN6EsRW')
      .then((result) => {
        console.log(result.text);
        alert("Email Sent!")
        e.target.reset()
      }, (error) => {
        console.log(error.text);
      });
  };

  return (
    <section id="contact" data-aos="fade-up">
      <div className="container">
        <div className="row">
          <div className="col-lg-5 col-xl-5 col-md-12 col-sm-12 contact-info" data-aos="fade-up">
            <div className="location">
              <i className="fa-solid fa-location-dot"></i>
              <div className="all-locations">
                <p>Adma - Fatqa</p>
                <p>kfahbab - kfaryassin</p>
              </div>
            </div>
            <div className="email" data-aos="fade-up">
              <i className="fa-regular fa-envelope"></i>
              <a href="mailto:walideliashachem@gmail.com">walideliashachem@gmail.com</a>
            </div>
            <div className="number" data-aos="fade-up">
              <i className="fa-solid fa-phone-volume"></i>
              <a href="tel:+96176488225">76-488 225</a>
            </div>
          </div>
          <div className="col-lg-7 col-xl-7 col-md-12 col-sm-12">
            <div className="container" data-aos="fade-up">
              <div className="row">
                <div className="col-lg-12 col-xl-12 col-md-12 col-sm-12">
                  <h1>Contact Us</h1>
                  <form ref={form} onSubmit={sendEmail} data-aos="fade-up">
                    <div className="row">
                      <div className="col-lg-6 col-md-12 col-sm-12">
                        <div className="form-group">
                          <input type="text" name="firstname" className="form-control" placeholder="First Name" required />
                        </div>
                      </div>
                      <div className="col-lg-6 col-md-12 col-sm-12">
                        <div className="form-group">
                          <input type="text" name="lastname" className="form-control" placeholder="Last Name" required />
                        </div>
                      </div>
                      <div className="col-lg-6 col-md-12 col-sm-12">
                        <div className="form-group">
                          <input type="email" name="email" className="form-control" placeholder="Email Address" required />
                        </div>
                      </div>
                      <div className="col-lg-6 col-md-12 col-sm-12">
                        <div className="form-group">
                          <input type="text" name="phone" className="form-control" placeholder="Phone Number" required />
                        </div>
                      </div>
                      <div className="col-lg-12 col-md-12 col-sm-12">
                        <div className="form-group">
                          <textarea className="form-control" name="message" placeholder="Message..."></textarea>
                        </div>
                      </div>
                      <div className="col-lg-12 col-md-12 col-sm-12">
                        <div className="form-group">
                          <button type="submit" className="btn-submit">
                            SEND<span></span>
                          </button>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        
      </div>
      <div>
      <div className="copyright text-center bg-black">
          <hr />
          <small > 2024 <a style={{color:"rgba(234,0,0,0.9"}} target="_blank" rel="noreferrer" href='https://www.instagram.com/hadi.hach?igsh=MWx0MGNqaGlnN2xwcw=='>Hadi Hachem</a>. All Rights Reserved</small>
        </div>
      </div>
    </section>
  );
};

export default Footer;
