import React, { useEffect, useState } from 'react'
import './Breadcrum.css'
import { Link } from 'react-router-dom';

export default function Breadcrum(props) {

    const {product} = props


    const [showBg, setShowBg] = useState(false);

    useEffect(() => {
      window.addEventListener('scroll', handleScroll);
  
      // Cleanup the event listener when the component unmounts
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
    }, []);
  
  
    const handleScroll = () => {
      if (window.scrollY >= 100 ) {
        setShowBg(true);
        
      } else {
        setShowBg(false);
      }
    };

   
  return (
    <div className=' breadcrum' style={{ background: showBg ? '#f0eaea' : 'transparent',color: showBg ? '#5c5c5c' : 'white'}}>
        <Link  style={{color: showBg ? '#5c5c5c' : 'white',transition: "0.5s" }} to="/">Home</Link> <i className="fa-solid fa-chevron-right"></i>
       <Link  style={{color: showBg ? '#5c5c5c' : 'white',transition: "0.5s" }} to={`/${product.category}`}> {product.category} </Link><i className="fa-solid fa-chevron-right"></i> {product.name}
    </div>
  )
}