import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/free-mode';
import 'swiper/css/navigation';
import 'swiper/css/thumbs';
import './SwiperProduct.css';
import { FreeMode, Navigation, Thumbs } from 'swiper/modules';

const SwiperProduct = (props) => {
  const { product } = props;
  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  return (
    <>
      <Swiper
        spaceBetween={10}
        navigation={true}
        thumbs={{ swiper: thumbsSwiper }}
        modules={[FreeMode, Navigation, Thumbs]}
        className="mySwiper22"
      >
        <section id="offers">
        <div className="container">
          {/* <div className="titles">
            <h6>New Offers</h6>
            <h1>
              <span>For Sale</span>
            </h1>
          </div> */}
          <div className="product-content swiper-content">
            <div className="product swiper mySwiper">
              {product.sub_images.map((image, index) => (
                <SwiperSlide key={index}>
                  <img src={image} alt={`Slide ${index}`} />
                </SwiperSlide>
              ))}
            </div>
          </div>
        </div>
      </section>
      </Swiper>
      <div className="container">
      <h6 className='productName'>{product.name}</h6>
      </div>
      {/* Thumbs Swiper */}
      <Swiper
        onSwiper={setThumbsSwiper}
        spaceBetween={10}
        slidesPerView={6}
        freeMode={true}
        watchSlidesProgress={true}
        modules={[FreeMode, Navigation, Thumbs]}
        className="mySwiperImages"
      >
        {product.sub_images.map((image, index) => (
          <SwiperSlide key={index}>
            <img src={image} alt={`Slide ${index}`} />
          </SwiperSlide>
        ))}
      </Swiper>

    </>
  );
};

export default SwiperProduct;
