import React from 'react';
import walidImage from '../Assets/walid.jpg';
import './AboutMe.css';

const AboutSection = () => {
  return (
    <section id="about">
      <div className="container">
        <div className="titles-aboutMe">
          <h6>About</h6>
          <h1>The journey</h1>
        </div>
        <div className="learn-about">
          <div className="about-img">
            <img
              className="about-img animate__animated animate__fadeInUp animate__delay-2s"
              src={walidImage}
              alt="Walid Hachem"
            />
            <div className="bg-black-img"></div>
          </div>
          <div className="about-me">
            <h2>Your Trusted Real Estate Partner</h2>
            <p className="aboutme">
              I am Walid Hachem, your dedicated real estate agent specializing in the vibrant communities
              of Adma, Kfarhbab, and Kfaryasin. With a passion for real estate and a commitment to excellence,
              I am here to guide you through the exciting journey of buying, selling, or investing in
              properties in these beautiful locales.
              With a wealth of experience in the real estate industry, I have been fortunate to witness the
              unique charm and growth of Adma, Kfarhbab, and Kfaryasin throughout the years. My extensive
              knowledge of these areas allows me to provide you with valuable insights, ensuring that your
              real estate transactions are not only successful but also tailored to your individual needs.
            </p>
          </div>
        </div>
      </div>
    </section>
  );
};

export default AboutSection;
