import React, { useState, useEffect } from 'react';
import './Navbar.css';
import { Link } from 'react-router-dom';
import logo from '../Assets/logo.png';

const Navbar = () => {
  const [showNavbar, setShowNavbar] = useState(false);
  const [showBg, setShowBg] = useState(false);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    // Cleanup the event listener when the component unmounts
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);


  const handleScroll = () => {
    if (window.scrollY >= 100) {
      setShowBg(true);
    } else {
      setShowBg(false);
    }
  };

  const scrollToTop = () => {
    window.scrollTo(0, 0);
  };

  return (
    <header className="header" id="header">
      <nav className="navbar" style={{ background: showBg ? 'white' : 'transparent', 
      transition:'0.5s',
      
      }}>
        <Link onClick={scrollToTop}  className="logo animate__animated animate__fadeInUp animate__delay-1s" to="/">
          <img src={logo} alt="" />
        </Link>
        <div  className={`nav-menu ${showNavbar?"open":""}`} id="nav-menu">
          <ul className="nav-list" style={{color: showBg?'rgba(228, 19, 19, 0.773)':'white'}}>
          <div className="nav-toggle" id="nav-toggle">
          <i
            onClick={() => {
              setShowNavbar(!showNavbar);
            }}
            onMouseEnter={() => {
                setShowNavbar(true);
              }}
              style={{transition: !showNavbar?"0.5s":"0.4s"}}
            className='ri-close-line nav-close animate__animated animate__fadeInUp animate__delay-0.5s'
          ></i>
        </div>
            <li className="nav-item">
              <Link  to="/" className="nav-link" onClick={() => {setShowNavbar(!showNavbar);scrollToTop();}} >
                Home
              </Link>
            </li>
            {/* <li className="nav-item">
              <Link to="/" offset={-500} className="nav-link" onClick={() => setShowNavbar(!showNavbar)}>
                About
              </Link>
            </li> */}
            <li className="nav-item">
              <Link to="/sale" className="nav-link" onClick={() => {setShowNavbar(!showNavbar);scrollToTop();}}>
                For Sale
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/rent" className="nav-link" onClick={() => {setShowNavbar(!showNavbar);scrollToTop();}}>
                For Rent
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/faqs" className="nav-link" onClick={() => {setShowNavbar(!showNavbar);scrollToTop();}}>
                FAQs
              </Link>
            </li>
            {/* <li className="nav-item">
              <Link to="clients" className="nav-link" onClick={() => setShowNavbar(!showNavbar)}>
                Clients
              </Link>
            </li> */}
            {/* <li className="nav-item">
              <Link to="contact" className="nav-link" onClick={() => setShowNavbar(!showNavbar)}>
                Contact
              </Link>
            </li> */}
          </ul>
        </div>

        {/*  */}

        <div className="nav-toggle" id="nav-toggle">
          <i
            onClick={() => {
              setShowNavbar(!showNavbar);
            }}
            // onMouseEnter={() => {
            //     setShowNavbar(true);
            //   }}
              style={{transition: !showNavbar?"0.5s":"0.4s"}}
            className='ri-function-line nav-open animate__animated animate__fadeInUp animate__delay-0.5s'
          ></i>
        </div>
      </nav>
    </header>
  );
};

export default Navbar;
